import "./Form.scss";

const Form = () => {
  return (
    <section className="form">
      <div className="form__content">
        <div className="form__textcolumn">
          <h2 className="form__title">  </h2>
          <p className="form__subtitle">    ?</p>
        </div>
        <form className="form__form">
          <input type="email" placeholder=" e-mail" />
          <input type="phone" placeholder=" " />
          <input type="name" placeholder=" " />
          <textarea />
          <div>
            <input type="checkbox" />
            <p>
                    
              <span>  </span>
            </p>
          </div>
          <button
            type="submit"
            onClick={(e) => {
              e.preventDefault();
            }}
          >
            
          </button>
        </form>
      </div>
    </section>
  );
};

export default Form;
